<template>
    <ul class="switches">
        <li
            class="switch-item"
            v-for="(item,index) in switches"
            :key="index"
            :class="{'active':currentIndex === index}"
            @click="switchItem(index)"
        >
            <span>{{item.name}}</span>
        </li>
    </ul>
</template>

<script type="text/ecmascript-6">
export default {
    props: {
        switches: {
            type: Array,
            default: function() {
                return []
            }
        },
        currentIndex: {
            type: Number,
            default: 0
        }
    },
    methods: {
        switchItem(index) {
            this.$emit('switch', index)
        }
    }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import 'common/stylus/variable'
.switches
    display flex
    align-items center
    width 120px
    margin 0 auto
    .switch-item
        flex 1
        padding 8px
        text-align center
        font-size $font-size-medium
        color $color-theme
        border 1px solid $color-theme
        border-radius 0 14px 14px 0
        &:nth-of-type(1)
            border-radius 14px 0 0 14px
        &.active
            background $color-theme
            color $color-text
</style>