<template>
    <div class="sort">
        <div class="header">
            <div class="back" @click="back">
                <i class="icon-arrow icon-arrow-l"></i>
            </div>
            <div class="switches-wrapper">
                <switches @switch="switchItem" :switches="switches" :currentIndex="currentIndex"></switches>
            </div>
            <div class="search" @click="search">
                <i class="search-icon"></i>
            </div>
        </div>
        <div class="nav">
            <ul class="slide">
                <li
                    v-for="(item,index) in categoryList"
                    @click="getBookTypes(item.type_id, index)"
                    :key="index"
                    :class="index==categoryActive?'active':''"
                >{{item.type_name}}</li>
            </ul>
            <cube-scroll
                ref="scroll"
                :data="bookList"
                :options="options"
                @pulling-up="onPullingUp"
                class="book-list-wrap"
            >
                <!-- <vert-more :booklist="bookList"></vert-more> -->
                <div class="book-list">
                    <router-link
                        class="book-item"
                        v-for="book in bookList"
                        :key="book.book_id"
                        :to="'/book/'+book.book_id"
                    >
                        <div class="cover">
                            <img :src="book.book_cover" />
                        </div>
                        <div class="text">
                            <h3>{{book.book_name}}</h3>
                            <p class="brief">{{book.book_brief}}</p>
                            <div class="other">
                                <span class="author">{{book.author_name}}</span>
                                <span class="category">{{book.category_name}}</span>
                                <span class="status status2" v-if="book.book_is_action==true">完结</span>
                                <span class="status" v-else>连载</span>
                            </div>
                        </div>
                    </router-link>
                </div>
            </cube-scroll>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
import Switches from 'components/switches/switches'
// import VertMore from 'components/book/vert-more'

export default {
    data() {
        return {
            currentIndex: 0,
            switches: [
                {
                    name: '男生'
                },
                {
                    name: '女生'
                }
            ],
            categoryList: [], // 分类
            bookList: [], // 书籍列表
            height: 0,
            categoryActive: 0,
            scrollTop: 0,
            scrollHeight: 0,
            sexType: 1, // 性别标识 男1 女2
            pageIndex: 1, // 初始页面1
            typeId: 0,
            isShowMore: false, // 是否获取到更多数据
            pullUpLoad: true,
            pullUpLoadThreshold: 0,
            pullUpLoadMoreTxt: '加载中...',
            pullUpLoadNoMoreTxt: '没有更多数据啦'
        }
    },
    computed: {
        options() {
            return {
                pullUpLoad: this.pullUpLoadObj
            }
        },
        pullUpLoadObj: function() {
            return this.pullUpLoad
                ? {
                      threshold: parseInt(this.pullUpLoadThreshold),
                      txt: {
                          more: this.pullUpLoadMoreTxt,
                          noMore: this.pullUpLoadNoMoreTxt
                      }
                  }
                : false
        }
    },
    mounted() {
        this.getBookSort()
        this.$nextTick(() => {
            this.$refs.scroll.refresh()
        })
    },
    methods: {
        switchItem(index) {
            this.currentIndex = index
            this.getBookSort(index + 1)
        },
        back() {
            this.$router.back()
        },
        search() {
            console.log('点击了搜索Search..')
        },
        changeHandler(label) {
            console.log('changed to:', label)
        },
        stickyChangeHandler(current) {
            console.log('sticky-change', current)
        },
        getBookSort(num) {
            this.sexType = num ? num : this.sexType
            this.categoryActive = 0
            let gender = this.sexType == 2 ? 0 : 1
            let data = {
                type: 1,
                gender: gender
            }
            this.$post('/book/ranks', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    this.categoryList = res.result
                    let typeId = res.result[0].type_id
                    this.getBookTypes(typeId)
                }
            })
        },
        getBookTypes(id, idx) {
            // 获取书籍
            if (id) {
                // 切换分类，初始化
                this.typeId = id
                this.pageIndex = 1
                this.bookList = []
                this.isShowMore = false
                this.pullUpLoad = true
            }
            if (idx || idx === 0) {
                console.log('index:' + idx)
                this.categoryActive = idx
                // console.log('scrollHeight:'+ this.scrollHeight)
                // this.scrollTop = -this.scrollHeight * idx;
                // this.scrollTop = -this.scrollHeight;
            }
            let data = {
                type_id: this.typeId,
                pn: this.pageIndex
            }
            this.$post('/book/types', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    let result = res.result.book_list
                    this.bookList = this.bookList.concat(result)

                    this.isShowMore = result && result.length
                    if (this.isShowMore) {
                        //有新数据
                        setTimeout(() => {
                            this.$refs.scroll.refresh()
                        }, 500)
                    } else {
                        const toast = this.$createToast({
                            txt: '暂无更多书籍',
                            type: 'txt',
                            mask: true,
                            time: 2000
                        })
                        toast.show()
                        this.pullUpLoad = false
                        this.$refs.scroll.forceUpdate()
                    }
                }
            })
        },
        onPullingUp() {
            // 更新数据
            setTimeout(() => {
                this.pageIndex += 1
                this.getBookTypes()
            }, 1000)
        }
    },
    components: {
        Switches,
        // VertMore
    }
}
</script>

